.footer {
  position: relative;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%
}

.sliding-background {
  background: url('./img/factsolotlBubbles.png')
  repeat-y;
  width: 100%;
  height: 400%;
  animation: slide 60s linear infinite;
  position: absolute;
  z-index: -1;
}


@keyframes slide{
  0%{
    transform: translate3d(0, 0, 0);
  }
  100%{
    transform: translate3d(0, -100%, 0);
  }
}
